/************************************

        [ INDEX ]

    I- Background Management
   II- Width and Height Management
  III- Button Management
   IV- Box Management
    V- ScrollBar Manipulation
   VI- Border Manipulation
  VII- Meme Generator Styles
    
*************************************/
    body{
        font-family: 'Inter' , sans-serif !important;
    }

    .pointer{
        cursor:pointer;
    }
    
/************************************
    I - Background Management
*************************************/
 
   

    .bg-purple-gradient{
        background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    }

    .bg-blue-gradient{
        background: linear-gradient(90deg, #194a8d 1.18%, #75c0e0 100%);
    }
    
    .bg-dark-gradient{
        background: linear-gradient(90deg, #212529 1.18%, #495057 100%);
    }

    .bg-blue{
        background-color:#194a8d !important;
    }
    .bg-darker{
        background-color: #1A1B21 !important;
    }

    .bg-darker-dark{
        background-color:#161619;
    }

    .bg-dark-night{
        background-color:#23252C;
    }

    .bg-orange{
        background-color:orange;
    }

/************************************
  II - Width and Height Management
*************************************/

   


    .h-min-800{
        min-height: 800px;
    }

    .h-min-700{
        min-height: 700px;
    }
    
    .h-min-600{
        min-height: 600px;
    }    

    .h-min-500{
        min-height: 500px;
    }

    .h-min-400{
        min-height: 400px;
    }

    .h-min-300{
        min-height: 300px;
    }

    .h-min-200{
        min-height:200px;
    }
    
    .h-min-100{
        min-height:100px;
    }

    .h--400{
        height:400px;
    }

    .h--300{
        height:300px;
    }

    .h--200{
        height:200px;
    }


    .h--100{
        height:100px;
    }

    .w-max-1000{
        max-width:800px;
    }

    .w--auto{
        width:auto;
    }

    @media(max-width:1000px){
        .w--auto{
            width:100%;
        }
    }

/************************************
  III - Button Management
*************************************/

    .clicked{
        background-color:gray !important;
        color:white;
    }

/************************************
  IV - Box Management
*************************************/
    .fruit-box{
        max-height:200px;
        overflow-y:visible;
        overflow-y: auto;
    }

    .circle{
        height:100px;
        width:100px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
    }

/***********************************
  V - ScrollBar Manipulation
***********************************/

    ::-webkit-scrollbar {
        width: 10px;
    }

    ::-webkit-scrollbar-thumb {
        background: #343a40;
        border-radius: 20px;
    }

/***********************************
 VI - Border Manipulation
***********************************/
    .border-pill{
        border-radius: 50rem;
    }

/***********************************
 VII - Meme Generator Styles
***********************************/
    .meme {
        position:relative;
    }

    .meme-text-top {
        z-index: 1;
        position:absolute;
        top:0;
        left:5%;
        right:5%;
        margin-top:10px;

        font-family: 'Impact';
        font-style: normal;
        font-weight: 900;
        /* font-size: 32px; */
        line-height: 39px;
        text-align: center;
        padding: 0 5px;
        font-size: 2.5em;
        text-transform: uppercase;
        color: white;
        letter-spacing: 1px;
        text-shadow:2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0px 2px 0 #000,
        2px 0px 0 #000,
        0px -2px 0 #000,
        -2px 0px 0 #000,
        2px 2px 5px #000;
    }

    .meme-text-bottom {
        z-index:1;
        position:absolute;
        bottom:0;
        left:5%;
        right:5%;
        margin-bottom:10px;

        font-family: 'Impact';
        font-style: normal;
        font-weight: 900;
        /* font-size: 32px; */
        line-height: 39px;
        text-align: center;
        padding: 0 5px;
        font-size: 2.5em;
        text-transform: uppercase;
        color: white;
        letter-spacing: 1px;
        text-shadow:2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0px 2px 0 #000,
        2px 0px 0 #000,
        0px -2px 0 #000,
        -2px 0px 0 #000,
        2px 2px 5px #000;
    }
/***********************************/

